import {
    Button,
} from '@wordpress/components';

const Modal = props => {
    return <div className={`premium-search-modal${props.isOpen ? ' open' : ''}`}>
        <div className="premium-modal-actions">
            <Button onClick={() => props.setIsOpen(false)}>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" role="img" aria-hidden="true" focusable="false"><path d="M13 11.8l6.1-6.3-1-1-6.1 6.2-6.1-6.2-1 1 6.1 6.3-6.5 6.7 1 1 6.5-6.6 6.5 6.6 1-1z"></path></svg>
            </Button>
        </div>
        <div className="premium-modal-content">
            {props.children}
        </div>
    </div>
}

export default Modal